import React,{useState,useEffect} from 'react'
import './Profile.css'
import EditIcon from '@material-ui/icons/Edit';
import axios from 'axios'
import SendIcon from '@material-ui/icons/Send';
import NotInterestedIcon from '@material-ui/icons/NotInterested';
import { useContext } from 'react'
import {ImageContext} from '../../context/ImageContext'


function Profile(props) {
    const [url,seturl] = useContext(ImageContext)

    
    const [namefield,setNameField] = useState(false)
    const [namefield2,setNameField2] = useState(false)
    const [usernamefield,setUsernameField] = useState(false)
    const [aboutfield,setAboutField] = useState(false)
    const [profiledata,setprofiledata] = useState({})
    const [name,setName] = useState("")
    const [name2,setName2] = useState("")
    const [username,setUserName] = useState("")
    const [about,setAbout] = useState("")
    const [show,setShow] = useState(false)
    const [selectedFile,setSelectedFile] = useState()
    const config = {headers:{"Content-Type":"multipart/form-data",accept:'application/json'}}
    

    let url1 = `http://127.0.0.1:8000/profile/get/${props.userdata?.id}/`;
    let url2 = `http://127.0.0.1:8000/profile/update/${props.userdata?.id}/`;
    let url3 = `http://127.0.0.1:8000/profile/updatedp/${props.userdata?.id}/`;
    
    var profilestyles = {
        height:"200px",
        width:"200px",
        position:"relative",
        border:"5px solid #fff",
        borderRadius: "50%",
        backgroundImage: `url(${process.env.REACT_APP_API_URL}${profiledata.image})`,
        backgroundSize:"contain",
        margin: "100px auto",
        overflow:"hidden"   
    }

    var myfile = {
        position:"absolute",
        bottom:"0",
        outline:"none",
        color:"transparent",
        width:"100%",
        boxSizing:"border-box",
        padding:"15px 120px",
        cursor:"pointer",
        transition:"0.5s"

    }
                    
    

    const onChangeName = ()=>{
        
        const resp = axios.patch(url2,{"fist_name":name})
        profiledata.first_name = name
        setNameField(false)
    }

    const onChangeName2 = ()=>{
        
        const resp = axios.patch(url2,{"last_name":name2})
        profiledata.last_name = name2
        setNameField2(false)
    }

   

    const onChangeUsername = ()=>{
        const resp = axios.patch(url2,{"username":username})
        profiledata.username = username
        setUsernameField(false)
    }

    const onChangeAbout = ()=>{
        
        const resp = axios.patch(url2,{"about":about})
        profiledata.about = about
        setAboutField(false)
    }

    
        useEffect(()=>{
            async function fetchData(){
        
            const resp = await axios.get(url1)
            setprofiledata(resp.data)
           
                      
        }  

            fetchData()

   },[])

    const clickHandlerA = ()=>{
        setNameField(!namefield)
    }

    const clickHandlerD = ()=>{
        setNameField2(!namefield2)
    }


    const clickHandlerB = ()=>{
        setUsernameField(!usernamefield)
    }
    const clickHandlerC = ()=>{
        setAboutField(!aboutfield)
    }

   
    const onClickHandler = async (e) => { // take the event as a parameter here
        e.preventDefault(); // Prevent form submission
        const data = new FormData()
        data.append("image",selectedFile,selectedFile.name)
        console.log(data)
        try{
            const resp = await axios.patch(url3, data,config)
           
        }
        catch(err){
            console.log(err.response)
        }
    }


    if(props.userdata){

    return (
        <div className="profile">
            <div className="profile-banner">
               <h1> Welcome to your Profile !</h1>
            </div>

            <div className="profile-picture" style={profilestyles}>
            
                <form method="patch" encType="multipart/formdata" 
            onSubmit={onClickHandler}>
                
                    <input type="file" 
                    accept="image/*"
                    name="dp" 
                    style={myfile}
                    onChange={(e) => setSelectedFile(e.target.files[0])}/>
                    
                </form>
                
                

            </div>

            <div className="personal-section">
               
                <div className="name">
                <div className="section">
                <h1>First Name</h1>
                </div>
                {!namefield?
                <h1>{profiledata.first_name}</h1>
                :
                <div className="conditioned-input">
                <input 
                type="text"
                defaultValue={profiledata.first_name}
                onChange={(e)=>{setName(e.target.value)}}
                />
                <button onClick={onChangeName}>Save</button>
                </div>
                }
                <EditIcon className="editicon" onClick={clickHandlerA}/>
                </div>

                <div className="name">
                
                <div className="section">
                <h1>Last Name</h1>
                </div>
                {!namefield2?
                <h1>{profiledata.last_name}</h1>
                :
                <div className="conditioned-input">
                <input 
                type="text"
                defaultValue={profiledata.last_name}
                onChange={(e)=>{setName2(e.target.value)}}
                />
                <button onClick={onChangeName2}>Save</button>
                </div>
                }
                <EditIcon className="editicon" onClick={clickHandlerD}/>
                </div>



                <div className="name">
                
                <div className="section">
                <h1>Username</h1>
                </div>
                {!usernamefield?
                <h1>{profiledata.username}</h1>
                :
                <div className="conditioned-input">
                <input 
                type="text"
                defaultValue={profiledata.username}
                onChange={(e)=>{setUserName(e.target.value)}}
                />
                <button onClick={onChangeUsername}>Save</button>
                </div>
                }
                <EditIcon className="editicon" onClick={clickHandlerB}/>
                </div>

                <div className="name">
                <div className="section">
                <h1>Email</h1>
                <h1>{props.userdata.email}</h1>
                </div>
                <NotInterestedIcon className="editicon" />
                </div>

                <div className="name">
                
                <div className="section">
                <h1>About</h1>
                </div>
                {!aboutfield?
                <h1>{profiledata.about}</h1>
                :
                <div className="conditioned-input">
                <textarea 
                type="text"
                defaultValue={profiledata.about}
                onChange={(e)=>{setAbout(e.target.value)}}
                />
                <button onClick={onChangeAbout}>Save</button>
                </div>
                }
                <EditIcon className="editicon" onClick={clickHandlerC}/>
                </div>
            

              </div>
  
            </div>
            
       
    )}
    else{
        return <h1>Loading</h1>
    }
}



export default Profile
  